export const aboutModule = {
  state: () => ({
    aboutArray: [
      //   []   
      { id: 1, icon: 'icon.png', content: "[  15 ]      " },
      { id: 2, icon: 'icon.png', content: " [   ]" },
      { id: 3, icon: 'icon.png', content: " [0 ]" },
      { id: 4, icon: 'icon.png', content: " [0 ]" },
      { id: 5, icon: 'icon.png', content: " [ ] "},
      { id: 5, icon: 'icon.png', content: " [ ] "},
      { id: 5, icon: 'icon.png', content: " [ ] "},
    ],
  }),
  getters: {
    
  },
  mutations: {
    aboutString(state) {
      state.aboutArray.forEach(benefitsItem => {
        const regex = /(?<=\[)(.*?)(?=\])/;
        const str = benefitsItem.content;
        const mark = str.match(regex)[0];
        const resultString = str.replace(mark, '');
        const resultArr = resultString.split('[]')

        benefitsItem.structure = {
          firstText: resultArr[0],
          mark: mark,
          lastText: resultArr[1],
        }
      });
    }
  },
  actions: {

  },
  namespaced: true
}