export const benefitsModule = {
  state: () => ({
    benefitsArray: [
      //   []    
      { id: 1, content: " [ 1200 ]" },
      { id: 2, content: " [ 120 ]" },
      { id: 3, content: " [50 000 ]" },
      { id: 4, content: " [ 1200 ]" },
      {id: 5,content: " [ ] "},
    ],
  }),
  getters: {
    
  },
  mutations: {
    benefitsString(state) {
      state.benefitsArray.forEach(benefitsItem => {
        const regex = /(?<=\[)(.*?)(?=\])/;
        const str = benefitsItem.content;
        const mark = str.match(regex)[0];
        const resultString = str.replace(mark, '');
        const resultArr = resultString.split('[]')

        benefitsItem.structure = {
          firstText: resultArr[0],
          mark: mark,
          lastText: resultArr[1],
        }
      });
    }
  },
  actions: {
    
  },
  namespaced: true
}